@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Ubuntu:wght@700&display=swap);
:root {
  --primary-color: #EC5252;
  --title-color: #322153;
  --title-color-800: #512e94;
  --text-color: #6C6C80;
  --text-color-light: #f0f0f0;
  --text-color-light-800: #f0f0f0da;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F0F0F5;
  -webkit-font-smoothing: antialiased;
  color: #6C6C80;
  color: var(--text-color);
}

body, input, button {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  color: #322153;
  color: var(--title-color);
  font-family: Ubuntu;
}

#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  }
  
  #header ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  #header ul li{
    float: left;
    margin: 0px 15px;
  }

  #header a {
    text-decoration: none;
  
    display: flex;
    align-items: center;
  }

  .selected{
    color: var(--title-color);
    font-weight: bold;
    text-decoration: none;
  
    display: flex;
    align-items: center;
  }
  
  #header a svg {
    margin-right: 16px;
    color: var(--primary-color);
  }

  @media only screen and (max-width: 425px) {
    #header img{
      width: 7em;
    }
    #header ul li {
      margin: 0px 10px;
  }
  }

#footer{
    background-color: #E5E5E5;
}

#footer .main{
    height: 160px;
    display: flex;
    padding-left: 24px;
    padding-right: 24px;
    align-items: center;
    justify-content: space-around;
    
}

#footer .botton{
    background-color: var(--title-color-800);
    display: flex;
    height: 40px;
    padding-left: 24px;
    padding-right: 24px;
    align-items: center;
    justify-content: space-between;
    color: var(--text-color-light);
}

#footer a{
    color: var(--title-color);
    text-decoration: none;
    display: flex;
    align-items: center;
}

#footer h4{
    margin: 6px auto;
}

#footer .botton span{
    color: #FE251B;
}

@media only screen and (max-width: 768px) {
    #footer p{
        font-size: .8em;
    } 
  }

  @media only screen and (max-width: 425px) {
    #footer h4, a, address{
        font-size: .9em;
    } 
    #footer .botton{
        display: flex;
        flex:1 0;
        justify-content: center;
        flex-direction: column;
    }
    #footer p{
        padding-bottom: .2em;
    } 
    #footer .main img{
        height: 1.5em;
    }
  }
