.grid-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    /* grid-template-rows: 1fr 3fr 0fr .2fr; */
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
      "header header header header header"
      "main main main aside aside"
      "form form form form form"
      "footer footer footer footer footer";
  }
  
  .header { grid-area: header; background-color: #f0f0f0da;}
  
  #main { grid-area: main; background-color: #E5E5E5;}
  
  .aside { grid-area: aside; background-color: #E5E5E5; }
  
  .form { grid-area: form; }
  
  .footer { grid-area: footer; }
  
  @media all and (-ms-high-contrast:none) {
    .grid-container {
      display: -ms-grid;
      -ms-grid-columns: repeat(5, 1fr);
      -ms-grid-rows: 2.5fr 3fr 2fr 1fr;
    }
  
    .header {
      -ms-grid-row: 1;
      -ms-grid-row-span: 1;
      -ms-grid-column: 1;
      -ms-grid-column-span: 5;
    }
  
    .main {
      -ms-grid-row: 2;
      -ms-grid-row-span: 1;
      -ms-grid-column: 1;
      -ms-grid-column-span: 3;
    }
  
    .aside {
      -ms-grid-row: 2;
      -ms-grid-row-span: 1;
      -ms-grid-column: 4;
      -ms-grid-column-span: 2;
    }
  
    .form {
      -ms-grid-row: 3;
      -ms-grid-row-span: 1;
      -ms-grid-column: 1;
      -ms-grid-column-span: 5;
    }
  
    .footer {
      -ms-grid-row: 4;
      -ms-grid-row-span: 1;
      -ms-grid-column: 1;
      -ms-grid-column-span: 5;
    }
  }

  @media only screen and (max-width: 768px) {
    .grid-container {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      /* grid-template-rows: 1fr 3fr 0fr .2fr; */
      grid-gap: 0px 0px;
      gap: 0px 0px;
      grid-template-areas:
        "header header header header header"
        "aside aside aside aside aside"
        "main main main main main"
        "form form form form form"
        "footer footer footer footer footer";
    }
  }
#page-home {
    width: 100%;
    margin: 0 auto;
    overflow-x: hidden;
}
  #page-home form {
    width: 100vw;
    margin: 4px auto;
    padding: 64px;
    background: #2A2040;
    /* border-radius: 8px; */
    display: flex;
    flex-direction: column;
  }
  #page-home form h1 {
    font-size: 36px;
  }
  #page-home form fieldset {
    min-inline-size: auto;
    border: 0;
    max-width: 730px;
    align-self: center;
  }
  #page-home form legend {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 40px;
  }
  #page-home form legend h2 {
    font-size: 24px;
    color: var(--text-color-light);
  }
  #page-home form legend span {
    font-size: 14px;
    font-weight: normal;
    color: var(--text-color-light-800);
  }
  #page-home form .field-group {
    flex: 1 1;
    display: flex;
  }
  #page-home form .field {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
  }
  #page-home form .field input[type=text],
  #page-home form .field input[type=email],
  #page-home form .field input[type=number] {
    flex: 1 1;
    background: #F0F0F5;
    border-radius: 8px;
    border: 0;
    padding: 16px 24px;
    font-size: 16px;
    color: #6C6C80;
  }
  #page-home form .field select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    flex: 1 1;
    background: #F0F0F5;
    border-radius: 8px;
    border: 0;
    padding: 16px 24px;
    font-size: 16px;
    color: #6C6C80;
  }
  #page-home form .field input::-webkit-input-placeholder {
    color: #A0A0B2;
  }
  #page-home form .field input:-ms-input-placeholder {
    color: #A0A0B2;
  }
  #page-home form .field input::-ms-input-placeholder {
    color: #A0A0B2;
  }
  #page-home form .field input::placeholder {
    color: #A0A0B2;
  }
  #page-home form .field label {
    font-size: 14px;
    margin-bottom: 8px;
    color:var(--text-color-light-800);
  }
  #page-home form .field :disabled {
    cursor: not-allowed;
  }
  #page-home form .field-group .field + .field {
    margin-left: 24px;
    justify-content: flex-end;
  }
  
  #page-home form .field-group input + input {
    margin-left: 24px;
    justify-content: flex-end;
  }
  
  #page-home form .field-check {
    flex-direction: row;
    align-items: center;
  }
  
  #page-home form .field-check input[type=checkbox] {
    background: #F0F0F5;
  }
  
  #page-home form .field-check label {
    margin: 0 0 0 8px;
  }
  
  #page-home form .leaflet-container {
    width: 100%;
    height: 350px;
    border-radius: 8px;
    margin-bottom: 24px;
  }
  
  #page-home form button {
    height: 51px;
    background: var(--primary-color);
    border-radius: 8px;
    color: #FFF;
    font-weight: bold;
    font-size: 16px;
    border: 0;
    padding: 0px 22px;
    transition: background-color 0.2s;
    cursor: pointer;
  }
  
  #page-home form button:hover {
    background: #dc5757;
  }
  
  .items-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
    gap: 16px;
    list-style: none;
  }
  
  .items-grid li {
    background: #f5f5f5;
    border: 2px solid #f5f5f5;
    height: 180px;
    border-radius: 8px;
    padding: 32px 24px 16px;
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  
    text-align: center;
  
    cursor: pointer;
  }
  
  .items-grid li span {
    flex: 1 1;
    margin-top: 12px;
  
    display: flex;
    align-items: center;
    color: var(--title-color)
  }
  
  .items-grid li.selected {
    background: #E1FAEC;
    border: 2px solid #34CB79;
  }

  .is-success{
    
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    z-index: 9999;
    background: #0E0A14;
    opacity: 0.95;
  }

  .hide{
    display: none;
    opacity: 0;
  }

  .is-success .alert{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 40%;
  }
  
  .is-success .alert h2{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    color: #F0F0F5;
  }

  .is-success .alert p{
    font-family: Roboto;
    font-style: normal;
    font-size: 19px;
    line-height: 37px;
    color: #F0F0F5;
  }

  .check-icon{
    margin-bottom: .5rem;
    color: #34CB79;
    font-size: xxx-large;
  }

  @media only screen and (max-width: 425px) {
    #page-home form legend {
      flex-direction: column;
    }

    #page-home form .field input[type=text],
    #page-home form .field input[type=email],
    #page-home form .field input[type=number] {
      border-radius: 4px;
      padding: .9em .9em;
      font-size: .9em;
    }

    #page-home form .field label {
      font-size: .9em;
      margin-bottom: 8px;
      color:var(--text-color-light-800);
    }
    #page-home form .field-group .field + .field {
      margin-left: .9em;
    }
    
    #page-home form .field-group input + input {
      margin-left: .9em;
    }
    #page-home form button {
      height: 43.9px;
      border-radius: 8px;
      font-weight: bold;
      font-size: .8em;
      border: 0;    
      padding: 7px 22px;
    }
    #page-home form button svg{
      visibility: hidden;
    }
  }
